import React, { Component } from 'react';
import styled from 'styled-components';
import Info from './Info';
import { FadeIn, FadeAndBounce } from './animations';

const profile = require('../images/profile.png');

const vh = `${window.innerHeight * 0.01}px`;

const Arrow = styled.a`
  grid-area: footer;
  opacity: 0;
  animation: ${FadeAndBounce} 2s forwards;
  animation-delay: 0.5s;
  transition: all 1s;
  font-size: 8rem;
  justify-self: center;
  align-self: end;
  text-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  text-decoration: none;
  color: white;
  &:hover {
    text-shadow: 0px 0px 3rem orange;
  }
`;

const Profile = styled.img`
  border-radius:50%;
  grid-area: image;
  opacity: 0;
  width: 80%;
  height: auto;
  justify-self: center;
  animation: ${FadeIn} 1s forwards;
  animation-delay: 0.5s
  text-align: center;
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
  @media (max-width: 1024px) {
    width: 80%;
  }
  `;

const Container = styled.div`
  background-image: linear-gradient(to bottom, #5700af, #5f0fb3, #661ab7, #6e23bb, #752bbf);
  height: 100vh;
  height: calc(${vh} * 100);
  color: white;
  display: grid;
  grid-template-columns: 0.10fr 0.40fr 0.40fr 0.10fr
  grid-template-rows: 0.10fr 0.70fr 0.20fr;
  grid-spacing: 20px;
  grid-template-areas:
    ". . . ."
    ". image info ."
    ". footer footer .";
  justify-items: center;
  font-size: 3rem;
  align-items: center;
  
  @media (max-width: 1367px) {
    font-size: 1.5rem;
  }

  @media (max-width: 1024px) {
    grid-template-columns: 1fr
    grid-template-rows: 0.15fr 0.30fr 0.10fr 0.30fr 0.10fr 0.05fr;
    grid-template-areas:
      "."
      "image"
      "."
      "info"
      "."
      "footer";
    justify-items: center;
    font-size: 1rem;
  }
`;

class Landing extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <Container id="landing">
        <Profile alt="profile" src={profile} />

        <Info />
        <Arrow href="#about" className="fa fa-chevron-down" />
      </Container>
    );
  }
}

export default Landing;
