import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';

const Container = styled.div`
  grid-area: ${props => props.area};
  text-align: center;
  border: 1px solid black;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  align-items: center;
  display: grid;
  background: white;
`;

const Image = styled.img`
  height: auto;
  cursor: pointer;
  position: relative;
  display: inline-block;
  width: 100%;
`;

class Project extends React.Component {
  constructor(props) {
    super(props);
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick() {
    const { imageClick, projectKey, title } = this.props;
    imageClick(projectKey, title);
  }

  render() {
    const { title, image, name } = this.props;
    return (
      <Container area={name}>
        <Image onClick={this.handleClick} alt={title} name={name} src={image} />
      </Container>
    );
  }
}

Project.propTypes = {
  title: PropTypes.string.isRequired,
  imageClick: PropTypes.func.isRequired,
  projectKey: PropTypes.number.isRequired,
  image: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
};

export default Project;
