import React, { Component } from 'react';
import PropTypes from 'prop-types';
import styled, { ThemeProvider } from 'styled-components';
import BaseModal from './baseComponents/BaseModal';
import Button from './baseComponents/Button';
import images from '../images/images';

const Link = styled.a`
  background: transparent;
  color: black;
  font-size: 1.5rem;
  text-decoration: none;
  border: 2px solid black;
  padding: 10px 15px;
  border-radius: 8px;
  margin: 5px;
  transition: all 0.35s;
  &:hover {
    color: white !important;
    background: rgb(180, 180, 180);
  }
  &:visited {
    color: black;
  }

  @media (max-width: 1024px) {
    font-size: 1rem;
  }
`;

class Modal extends Component {
  static getDerivedStateFromProps(nextProps, prevState) {
    if (prevState.display !== nextProps.display) {
      return { display: nextProps.display };
    }
    return { prevState };
  }

  constructor(props) {
    super(props);
    this.state = {
      display: 'hidden',
    };
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick(e) {
    e.preventDefault();
    const { closeModal } = this.props;
    closeModal();
  }

  render() {
    const { projectKey, title } = this.props;
    const { display } = this.state;
    const theme = {
      vis: display,
      back: '#FFFFFF',
    };
    if (display === 'visible') {
      return (
        <ThemeProvider theme={theme}>
          <BaseModal key="modal">
            <video muted height="100%" width="100%" autoPlay>
              <source src={images[projectKey].mp4} />
            </video>
            <h1 style={{ color: 'black' }}>{title}</h1>
            <h3 style={{ color: 'black' }}>{images[projectKey].description}</h3>
            <Button onClick={this.handleClick}>Close</Button>
            <Link href={images[projectKey].url}>View Site</Link>
            <Link href={images[projectKey].repo}>View Code</Link>
          </BaseModal>
        </ThemeProvider>
      );
    }
    return <div />;
  }
}

Modal.propTypes = {
  closeModal: PropTypes.func.isRequired,
  title: PropTypes.string.isRequired,
  projectKey: PropTypes.number.isRequired,
};

export default Modal;
