import React from 'react';
import styled from 'styled-components';
import Badges from './Badges';

const vh = `${window.innerHeight * 0.01}px`;

const Container = styled.div`
  background-image: linear-gradient(to bottom, #752bbf, #7e39c5, #8845cc, #9151d2, #9a5dd8);
  height: 100vh;
  height: calc(${vh} * 100);
  color: white;
  display: grid;
  grid-template-columns: 0.05fr 0.45fr 0.45fr 0.05fr
  grid-template-rows: 0.10fr 0.75fr 0.15fr;
  grid-column-spacing: 4rem;
  grid-template-areas:
    ". header header ."
    ". info image ."
    ". footer footer .";
  justify-items: center;
  font-size: 2rem;
  align-items: center;
  
  @media (max-width: 1367px) {
    font-size: 1.5rem;
  }

  @media (max-width: 1024px) {
    height: 100vh;
    grid-template-columns: 0.10fr 0.80fr 0.10fr;
    grid-template-rows: 0.15fr 0.35fr 0.40fr 0.10fr;
    grid-column-spacing: 0rem;
    grid-row-gap: 0.5rem;
    font-size: 1.25rem;
    grid-template-areas:
      ". header ."
      ". info ."
      ". image ."
      ". footer .";
    justify-items: center;
  }
`;

const Arrow = styled.a`
  grid-area: footer;
  opacity: 1;
  transition: all 1s;
  font-size: 8rem;
  justify-self: center;
  align-self: end;
  text-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  text-decoration: none;
  color: white;
  &:hover {
    text-shadow: 0px 0px 3rem orange;
  }
`;

const Title = styled.h1`
  font-size: 4rem;
  grid-area: header;
  justify-self: center;
  text-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  @media (max-width: 1024px) {
    font-size: 3rem;
  }
`;

const About = () => (
  <Container id="about">
    <Title>About</Title>
    <p style={{ gridArea: 'info', justifySelf: 'center' }}>
      My name is Luke Hendrick and I currently work with VoIP at Cisco Systems in Tulsa, OK.
      <br />
      {' '}
      <br />
I enjoy solving problems and simplifying workflows with code. At work, I mainly
      use C# and Python; I build personal things with React and NodeJS. Take a look at some of my
      projects down below, and let me know what you think!
    </p>
    <Badges />
    <Arrow href="#projects" className="fa fa-chevron-down" />
  </Container>
);

export default About;
