import React, { Component } from 'react';
import styled from 'styled-components';
import Project from './Project';
import projects from '../images/images';
import Modal from './Modal';

const vh = `${window.innerHeight * 0.01}px`;

const ProjectsDiv = styled.div`
  background-image: linear-gradient(to bottom, #9a5dd8, #a66be2, #b279eb, #be88f5, #ca96ff);
  display: grid;
  grid-template-columns: 0.25fr 0.85fr 0.85fr 0.25fr;
  grid-template-rows: 0.15fr 1fr 1fr 0.05fr;
  grid-template-areas:
    '. header header .'
    '. kanban food .'
    '. drum news .'
    '. footer footer .';
  justify-items: center;
  grid-row-gap: 1rem;
  grid-column-gap: 20px;
  align-items: center;
  height: 100vh
  height: calc(${vh} * 100);
  color: white;

  @media (max-width: 1024px) {
    grid-template-columns: 20% 60% 20%;
    grid-template-rows: 10% 20% 20% 20% 20% 10%;
    font-size: 1rem;
    grid-template-areas:
      '. header .'
      '. kanban .'
      '. food .'
      '. drum .'
      '.news .'
      '. footer .';
    grid-column-gap: 0rem;
    grid-row-gap: 1rem;
  }
`;
const Title = styled.h1`
  font-size: 4rem;
  grid-area: header;
  justify-self: center;
  text-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  @media (max-width: 1024px) {
    font-size: 3rem;
  }
`;
class Projects extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modal: 'hidden',
      title: '',
      projectKey: 0,
    };

    this.handleClick = this.handleClick.bind(this);
    this.closeModal = this.closeModal.bind(this);
  }

  handleClick(projectKey, title) {
    this.setState(() => ({ modal: 'visible', projectKey, title }));
  }

  closeModal() {
    this.setState({ modal: 'hidden', projectKey: 0, title: '' });
  }

  render() {
    const { modal, projectKey, title } = this.state;
    return (
      <ProjectsDiv id="projects">
        <Title>Projects</Title>
        {projects.map((project, index) => (
          <Project
            title={project.title}
            name={project.name}
            image={project.image}
            imageClick={this.handleClick}
            projectKey={index}
          />
        ))}
        <Modal display={modal} title={title} projectKey={projectKey} closeModal={this.closeModal} />
      </ProjectsDiv>
    );
  }
}

export default Projects;
